<template>
  <div>
    <div class="main-mine-container">
      <img class="mine-title-img" src="static/imgs/我的世界.png" />
      <ul class="my-list">
        <li
          class="my-link"
          @mouseover="showDelIcon(index)"
          @mouseout="hideDelIcon(index)"
          v-for="(item, index) of myLinks"
          :key="index"
        >
          <a :href="item.url" target="_blank">{{item.linkTitle}}</a>
          <img
            src="/static/imgs/关闭.png"
            class="delete-icon"
            :ref="`del${index}`"
            @click="handleDelClick(index)"
          />
        </li>
        <li class="my-link" @click="handleAddClick" ref="my_link">
          <img src="static/imgs/添加.png" class="add-icon" />
        </li>
      </ul>
    </div>
    <add-link-panel :style="panelStyle" @submit="handlePanelSubmit" v-if="showPanel"></add-link-panel>
  </div>
</template>

<script>
import AddLinkPanel from '@/pages/home/components/AddLinkPanel'
import axios from 'axios'
export default {
  name: 'MainMine',
  data () {
    return {
      panelStyle: {
        top: '',
        left: ''
      }
    }
  },
  computed: {
    siteNameList () {
      let list = new Set()
      for (let val of this.myLinks) {
        list.add(val.linkTitle)
      }
      return list
    },
    siteUrlList () {
      let list = new Set()
      for (let val of this.myLinks) {
        list.add(val.url)
      }
      return list
    },
    logStatus () {
      return this.$store.state.logStatus
    },
    showPanel () {
      return this.$store.state.showPanel
    },
    myLinks () {
      const favorStr = this.$store.state.favor
      const favor = []
      if (favorStr) {
        let list = favorStr.split(',')
        let couplesLen = list.length / 2
        for (let i = 1; i <= couplesLen; i++) {
          favor.push({ 'linkTitle': list[2 * i - 2], 'url': list[2 * i - 1] })
        }
      }
      return favor
    }
  },
  components: {
    AddLinkPanel
  },
  methods: {
    // 点击添加链接的回调函数
    handleAddClick () {
      event.stopPropagation()
      this.panelStyle.top = event.pageY + 35 + 'px'
      this.panelStyle.left = event.pageX - 25 + 'px'
      this.$store.commit('changeShowPanel', true)
    },
    // 在链接填写表单点击确认添加或点击快捷添加
    handlePanelSubmit (siteName, siteUrl) {
      if (this.siteNameList.has(siteName)) {
        alert('名称已存在！')
        this.$store.commit('changeShowPanel', false)
        return
      }
      if (this.siteUrlList.has(siteUrl)) {
        this.$store.commit('changeShowPanel', false)
        alert('网址已存在！')
        return
      }
      let newFavorStr = ''
      console.log(this.$store.state.favor)
      if (this.$store.state.favor) {
        newFavorStr = this.$store.state.favor + `,${siteName},${siteUrl}`
      } else {
        newFavorStr = `${siteName},${siteUrl}`
      }
      // this.myLinks.push({ linkTitle: siteName, url: siteUrl })
      // 请求发送整个模拟添加成功的favor字符串
      // 如果请求成功就提交到store
      axios.post('public/index.php/users/postRecord', {
        'data': {
          'name': 'add',
          'favor': newFavorStr
        }
      }).then(this.submitSucc)
      this.$store.commit('changeShowPanel', false)
    },
    // 添加请求成功
    submitSucc (res) {
      console.log(res)
      // 添加请求成功就往store提交
      if (res.status === 200) {
        let favor = JSON.parse(res.data.newdata).favor
        this.$store.commit('changeFavor', favor)
        console.log('succ:' + favor)
      }
    },
    // 点击删除按钮
    handleDelClick (index) {
      // 点击删除按钮时，要先构成一个删除的预览newFavorstr，然后用这个newFavorStr提交请求，请求成功之后就删除
      // 删除是通过vuex的响应来使列表重新渲染，并且更新store
      // 请求，把删除后的临时favor发送给后台
      // 如果请求成功，再将favor写回到store
      // 否则不操作

      const favorStr = this.$store.state.favor
      let favor = []
      if (favorStr) {
        let list = favorStr.split(',')
        let couplesLen = list.length / 2
        for (let i = 1; i <= couplesLen; i++) {
          favor.push({ 'linkTitle': list[2 * i - 2], 'url': list[2 * i - 1] })
        }
        // 删除
        favor.splice(index, 1)
      }

      let newFavorStr = ''
      for (let item of favor) {
        newFavorStr += item.linkTitle + ',' + item.url + ','
      }
      newFavorStr = newFavorStr.slice(0, -1)
      // this.$store.commit('changeFavor', newFavorStr)
      console.log(newFavorStr)
      // 发送请求
      // 如果请求成功就将数据写回到store
      axios.post('public/index.php/users/postRecord', {
        'data': {
          'name': 'delete',
          'favor': newFavorStr
        }
      }).then((res) => {
        console.log(res)
        if (res.status === 200) {
          this.$store.commit('changeFavor', newFavorStr)
        }
      })
    },
    // 使删除按钮显示
    showDelIcon (index) {
      this.$refs['del' + index][0].style.display = 'inline-block'
    },
    // 使删除按钮隐藏
    hideDelIcon (index) {
      this.$refs['del' + index][0].style.display = 'none'
    }
  },
  mounted () {
  }
}

</script>

<style scoped lang="stylus">
.main-mine-container
  min-width 800px
  position relative
  margin-top 4rem
  height auto
  width 100%
  background white
  padding 2.5rem 1rem 0.5rem 1rem
  border-radius 1rem
  border 1px solid rgb(189, 204, 212)

  .mine-title-img
    width 6.5rem
    position absolute
    left -1.6rem
    top -2rem

  .my-list
    padding 0 0 1rem 0
    width 100%
    display flex
    flex-wrap wrap

    .my-link
      width 21%
      margin 0.4rem 2%
      padding 0.5rem 1rem
      background #D7E7F2
      border-radius 1rem
      font-size 0.7rem
      display flex
      justify-content center
      align-items center
      cursor pointer
      position relative

      .delete-icon
        width 15px
        opacity 0.3
        position absolute
        right 0
        margin-right 15px
        transition all 0.8s
        display none

        &:hover
          transform scale(1.2)
          opacity 1

      .add-icon
        width 0.7rem
        opacity 0.7
</style>



